    // 
    submit() {
        this.creditResultInfoBean['productNo'] = this.productNo;
        this.creditResultInfoBean['custRequestNo'] = this.custRequestNo_new;
        this.creditResultInfoBean.startDate = this.commfunc.transDateN(this.startDate);
        this.creditResultInfoBean.endDate = this.commfunc.transDateN(this.endDate);
        this.creditResultInfoBean['oldCustRequestNo'] = this.custRequestNo;
        let params = {
            creditMap: this.creditResultInfoBean,
            attributeMap: this.createApplyBean,
            custRequestNo: this.custRequestNo_new,
            commonMap: this.commonMap,
        }
        this.creditCcmService.submitCreditCardUpdateItem(params).subscribe(data => {
            // -
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
            }
            if (s.extParam.specifyNextNode) {
                delete s.extParam.specifyNextNode
            }
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            if (data.returnCode.code == 'success') {
                commonHeader['globalSeqNo'] = '';
                this.route1.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page/approve-page']);
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        })
    }

