        this.creditCardElementBean.IN_isContrac = ''; // 
        this.creditCardElementBean.IN_contractStatus = ''; // 
        this.creditCardElementBean.IN_contractBalance = ''; // 
        this.creditCardElementBean.overdueMark = ''; // 
        this.creditCardElementBean.IN_coBorrowerList = []; // 
        this.creditCardElementBean.IN_loanUser1 = ''; // 1
        this.creditCardElementBean.IN_loanUser2 = ''; // 2
        this.creditCardElementBean.IN_loanUser3 = ''; // 3
