    // 
    judgmentDueDate() {
        let minStartMonth = moment(new Date(this.maximumInfoBean.applyDate)).add(this.dueDateScope.min, 'month').format('Y-MM-DD');
        let MaxEndMonth = moment(new Date(this.maximumInfoBean.applyDate)).add(this.dueDateScope.max, 'month').format('Y-MM-DD');
        this.dueDateScopeDate = {
            min: minStartMonth,
            max: MaxEndMonth
        }
    }
    // 
    queryTenLeven() {
        const params = {
            commonHeader: this.commonHeader,
            targetSysId: '304010',//
            clientNo: this.data.transferCreditData['custNo'],
            clientFlag: '0',//
            tranType: '2'  // 
        };
        this.creditCcmService.getTenLeven(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.list_array && data.list_array.length > 0) {
                    this.maximumInfoBean.tenClearStatus = data.list_array[0].ten_type;
                } else {
                    this.maximumInfoBean.tenClearStatus = null;
                }

            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });
    }
