    // ()   --
    queryDynamicProductAttributeOrValue() {
        const params = {
            'productId': this.data.transferCreditData['productNo'], //     
            'custRequestNo': this.data.transferCreditData['custRequestNo']//  
        };
        this.creditCcmService.queryProductAttributes(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                console.log("asd")
                data.productFeatureList.forEach(item => {
                    //
                    if (item.productFeatureTypeId == "PCA0000012") {
                        this.applicationScope = {
                            min: item.productFeatureList[0].min,
                            max: item.productFeatureList[0].max
                        }
                    }
                    // 
                    if (item.productFeatureTypeId == "PCA0000014") {
                        this.dueDateScope = {
                            min: item.productFeatureList[0].min,
                            max: item.productFeatureList[0].max
                        }
                        this.judgmentDueDate()//
                    }
                    // 
                    console.log("asd")
                    if (item.productFeatureTypeId == "PCA0000003") {
                        this.judgmentCurrency(item);//  
                    }
                    // 
                    if (item.productFeatureTypeId == "PCA0000022") {
                        this.judgmentCurrencyTwo(item);//  
                    }
                    // 
                    if (item.productFeatureTypeId == "PCA0010070") {
                        this.judgmentCurrencyTwos(item);//  
                    }
                })
                this.guaranteeModeOptionsChange();
                this.guaranteeModeOptionsChangeTwo()

            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        })
    }


