    // 
    queryNowTime() {
        let nowTime = {};
        let erro = '';
        let params = this.httpService.handleParams(nowTime)
        $.ajax({
            url: TZB_HTTP_CUS + API.queryNowTime,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: (data) => {
                if (data.returnCode.code == 'success') {
                    if (data.nowDate) {
                        let time = data.nowDate.split(' ');//2018-01-03 00:00:00
                        this.maximumInfoBean.applyDate = this.commfunc.transDateN(new Date(time[0]));
                        this.maximumInfoBean.tellerDate = this.commfunc.transDateN(new Date(time[0]));
                    }
                } else {
                    erro = data.returnCode.message;
                }
            },
            error: (err) => {
                erro = '';
            }
        })
        if (erro != '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: erro });
            return
        }
    }

