    // 
    // 
    //
    onKeyupNumber() {
        if (this.maximumInfoBean.applyAmt) {
            this.maximumInfoBean.applyAmt = this.commfunc.transform(this.maximumInfoBean.applyAmt) + '';
        }
        // 
        if (this.maximumInfoBean.applyAmt) {
            if (parseInt(this.maximumInfoBean.applyAmt) < parseInt(this.applicationScope.min)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' + this.applicationScope.min + '!' });
                this.maximumInfoBean.applyAmt = "";
                return
            }
        }
        if (this.maximumInfoBean.applyAmt) {
            if (parseInt(this.maximumInfoBean.applyAmt) > parseInt(this.applicationScope.max)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' + this.applicationScope.max + '!' });
                this.maximumInfoBean.applyAmt = "";
                return
            }
        }
        const regAmtJy = /^([1-9][0-9]*[0]{2})?$/;
        if (!regAmtJy.test(this.maximumInfoBean.applyAmt) && parseInt(this.maximumInfoBean.applyAmt) != 0) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '100!' });
            this.maximumInfoBean.applyAmt = "";
            return
        }
        this.maximumInfoBean.applyAmt = this.decimalPipe.transform(this.maximumInfoBean.applyAmt, '1.2-2');//
    }

    // 
    docheck(param) {
        if (this.maximumInfoBean.applyAmt != '' && this.maximumInfoBean.applyAmt != null && this.maximumInfoBean.applyAmt != undefined) {
            var regAmt = /^[0-9]*(\.[0-9]{0,2})?$/;
            if (!regAmt.test(param)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                this.maximumInfoBean.applyAmt = "";
                return
            }
        }
    }

    // 
    showDataTime() {
        let StartMonthDateString = null;
        let StartMonthDateMax = null;
        let EndMonthDateString = null;
        let EndMonthDateMin = null;

        if (this.dueDateScopeDate.min) {
            StartMonthDateString = this.commfunc.transDateN(this.dueDateScopeDate.min);
            StartMonthDateMax = new Date(this.getMinMaxDate(StartMonthDateString, 'min'))
        }

        if (this.dueDateScopeDate.max) {
            EndMonthDateString = this.commfunc.transDateN(this.dueDateScopeDate.max);
            EndMonthDateMin = new Date(this.getMinMaxDate(EndMonthDateString, 'max'))
        }
        if (StartMonthDateMax && EndMonthDateMin) {
            if (this.maximumInfoBean.endDate < StartMonthDateMax || this.maximumInfoBean.endDate > EndMonthDateMin) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' + StartMonthDateString + '' + EndMonthDateString + '' });
                this.maximumInfoBean.endDate = ""
            }
        }
    }
    // 
    getMinMaxDate(timeParam, param) {
        let time = new Date(timeParam);
        let year = time.getFullYear();
        let month = time.getMonth() + 1 + '';
        let data = time.getDate() + '';
        if (month.length < 2) {
            month = '0' + month;
        }
        if (data.length < 2) {
            data = "0" + data
        }
        let todayTime;
        if (param == 'min') {
            todayTime = year + "/" + month + "/" + data + ' 00:00:00';
        }
        if (param == 'max') {
            todayTime = year + "/" + month + "/" + data + ' 23:59:59';
        }
        return todayTime;
    }
