
import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Message } from 'primeng/primeng';
import { ParamsBean } from '../bean/params.bean';
import { ProductCountBean } from '../bean/product-count.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';
import { ParentChildCommunicationService } from 'app/pages/tzb/custom/credit-ccm/common/service/parent-child-comm.service';

@Component({
    selector: 'app-product-count',
    templateUrl: './credit-product-count.component.html',
    styleUrls: ['./credit-product-count.component.scss']
})

// tslint:disable-next-line:component-class-suffix
export class CreditProductCount implements OnInit {
    cqiId: any;
    colList: any;
    isbsave=false;
    endDate: any;      // 
    startDate: any;    // 
    applyAmt: any;     // 

    hiddenBtn: boolean; // 

    blockedPanel: boolean = false; // 

    productCountBean = new ProductCountBean(); // 

    paramsBean = new ParamsBean(); // 

    msgs: Message[] = []; // 
    isHistory: boolean = true;//
    isApprove: boolean = true;//
    userform: FormGroup;
    @Input('data') data;
    displayComponent: boolean = false;//
    // 
    constructor(
        public commfunc: Commfunc,
        private creditCcmService: CreditCcmService,
        private communicationService: ParentChildCommunicationService,
        private fb: FormBuilder
    ) {
        this.communicationService.queryTransfer.subscribe(_event => {
            if (_event && _event.bookmarkId && _event.componentNames.indexOf('CreditProductCount') !== -1) {
                this.getProductCountAttrFormDynamicProduct();
            }
            //star
		if(this.data.transferCreditData.isTrialbatch&&this.data.transferCreditData.isTrialbatch=='isTrialbatch'){
            this.isHistory=false;
            this.isApprove=false;
		}
        //end
        });
    }

    // 
    ngOnInit() {
        
        //console.log(this.data);
        //
        if (this.data.transferCreditData['taskType'] && this.data.transferCreditData['taskType'] == 'approve') {
            this.isApprove = false;
            this.userform = this.fb.group({
                'lastRrPn': new FormControl(''),
                'contributionRrPn': new FormControl(''),
                'thisRrPn': new FormControl(''),
            }); //
        }else{
            this.userform = this.fb.group({
                'lastRrPn': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]*(\.[0-9]{0,2})?$/)]),
                'contributionRrPn': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]*(\.[0-9]{0,2})?$/)]),
                'thisRrPn': new FormControl('', [Validators.required, Validators.pattern(/^[0-9]*(\.[0-9]{0,2})?$/)]),
            }); //
        }
        //
        if (this.data['transferCreditData']['flag'] && this.data['transferCreditData']['flag'] == '2') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
       
    }
    // 
    getProductCountAttrFormDynamicProduct() {
        const param = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
        };
        this.creditCcmService.dynamicProductQuery(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (!data.dynamicProductValue['PCA0010004']) {
                    this.msgs.push({ severity: 'error', summary: '', detail: '' + data.dynamicProductValue['PCA0010004'] });
                    return;
                }
                // 011
                const reg = /[\u4e00-\u9fa5]/;
                if (!reg.test(data.dynamicProductValue['PCA0010004'])) {
                    this.displayComponent = true;
                    this.promise();
                }else{
                    this.displayComponent = false;
                }
            } else {
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        });
    }
    // 
    promise() {
        let a = new Promise((resolve, reject) => {
            // -(yyyyMMdd)(yyyyMMdd)
            const params = {
                'custRequestNo': this.data.transferCreditData['custRequestNo']
                // 'custRequestNo':'562373',
            };
            this.creditCcmService.queryCreditInfo(params).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    this.applyAmt = data.creditMap.applyAmt;
                    this.startDate = data.creditMap.startDate.replace(/\-/g, '');;
                    this.endDate = data.creditMap.endDate.replace(/\-/g, '');
                    resolve({ applyAmt: this.applyAmt, startDate: this.startDate, endDate: this.endDate })
                } else {
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }]
                }
            })
        })

        let b = new Promise((resolve, reject) => {
            // -(PCA0000053)
            const param = {
                'custRequestNo': this.data.transferCreditData['custRequestNo'],
                // 'custRequestNo':'562373',
            };
            this.creditCcmService.dynamicProductQuery(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (!data.dynamicProductValue['accrualScalePct']) {
                        this.msgs.push({ severity: 'error', summary: '', detail: '' + data.dynamicProductValue['accrualScalePct'] });
                        return;
                    }
                    if (!data.dynamicProductValue['PCA0000053']) {
                        this.msgs.push({ severity: 'error', summary: '', detail: '' + data.dynamicProductValue['PCA0000053'] });
                        return;
                    }
                    resolve({ display: this.displayComponent, accrualScalePct: data.dynamicProductValue['accrualScalePct'], calculateProductAttr: data.dynamicProductValue['PCA0000053'] });
                } else {
                    this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });

                }
            });
        })

        //abc
        let c = Promise.all([a, b]);
        c.then((res) => {
            this.queryProductCountList(res[0]['applyAmt'], res[0]['startDate'], res[0]['endDate'], res[1]['accrualScalePct'], res[1]['calculateProductAttr'])

        })
    }

    // 
    queryProductCountList(loanAmt: string, loanDateStart: string, loanDateEnd: string, accrualScale: string, calculateProductAttr: string) {
        debugger
        const queryBean = {
            'custRequestNo': this.data.transferCreditData['custRequestNo'],
            // 'custRequestNo':'562373',
            'custName': this.data.transferCreditData.custName,
            'custNo': this.data.transferCreditData.custNo,
            'productAttr': calculateProductAttr,
            'loanAmt': loanAmt,
            'loanDateStart': loanDateStart,
            'loanDateEnd': loanDateEnd,
            'accrualScale': accrualScale,
            'countFlag': this.data.transferCreditData['flag'] || '1'
        };
        this.creditCcmService.calculateCustAccumulationList(queryBean).subscribe(data => {
            if (data.returnCode.code === 'success') {
                // 
                this.productCountBean.expectRrPn = Number(data.reductionValue); // 
                this.productCountBean.rrPn = Number(data.usedValue); // ()
                this.productCountBean.remainingValue = Number(data.reductionValue) - Number(data.usedValue); // ()
                if (data.list_array.length === 0) {
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    return;
                }
                this.paramsBean.queryList = this.filter(data.list_array); // 
                // /
                if (data.isSuccess === '1') {
                    this.msgs = [{ severity: 'success', summary: '', detail: '' }];
                    return;
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });
    }

    // 
    filter(list_array) {
        let list = [];
        list_array.forEach(item => {
            if (item.last_actual_rr_pn != '0.0' || item.this_actual_rr_pn != '0.0' || item.contribution_accrual != '0.0') {
                list.push(item);
            }
        });
        return list;
    }

    selected(col, ri) {
        this.colList = col;
        this.productCountBean.lastRrPn = col.last_used; // last_used
        this.productCountBean.thisRrPn = col.this_used; // this_used
        this.productCountBean.contributionRrPn = col.contribution_used; // contribution_used
    }

    updateProductCount() {
        this.isbsave=true;
        //
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty()
        };
        //
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                this.isbsave=false;
                return false
            };
        }

        if (this.paramsBean.selectBean) {
            if (Number(this.colList['last_actual_rr_pn']) < Number(this.productCountBean.lastRrPn)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.isbsave=false;
                return;
            }
            if (Number(this.colList['this_actual_rr_pn']) < Number(this.productCountBean.thisRrPn)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.isbsave=false;
                return;
            }
            if (Number(this.colList['contribution_accrual']) < Number(this.productCountBean.contributionRrPn)) {
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                this.isbsave=false;
                return;
            }
            let tempArray = [];
            tempArray = JSON.parse(JSON.stringify(this.paramsBean.queryList));
            for (let i = 0; i < tempArray.length; i++) {
                if (tempArray[i].cqiId === this.colList['cqiId']) {
                    tempArray[i].last_used = this.productCountBean.lastRrPn;
                    tempArray[i].this_used = this.productCountBean.thisRrPn;
                    tempArray[i].contribution_used = this.productCountBean.contributionRrPn;
                    break;
                }
            }
            //  
            const queryParam = {
                'reductionValue': this.productCountBean.expectRrPn,
                'custRequestNo': this.data.transferCreditData['custRequestNo'],
                'questionList': tempArray
            }
            this.creditCcmService.batchSaveAccumulationQuestion(queryParam).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    this.isbsave=false;
                    this.productCountBean.expectRrPn = Number(data.reductionValue);
                    this.productCountBean.rrPn = Number(data.usedValue);
                    this.productCountBean.remainingValue = Number(data.reductionValue) - Number(data.usedValue);
                    this.paramsBean.queryList = data.questionList;
                    //  0 1 
                    if (data.isSuccess === '1') {
                        this.msgs = [{ severity: 'success', summary: '', detail: '' }];
                        return;
                    }
                } else {
                    this.isbsave=false;
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            });
        } else {
            this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
        }
    }
}