	/**
	 * 
	 * 
	 * check 1., 
	 * check 2.,
	 * 
	 * check 4.
	 * a)()(010002)
	 * b)
	 * 
	 */
	checkRuleVerification(list) {
		// check1 start ...
		if (this.creditDetail.guaranteeType === '101' && this.creditDetail.contractMode === '01') {
			if (!this._check1()) {
				// 
				this.msgs = [];
				this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
				return false;
			}
		}
		// check1 end ...

		// check2 start ...
		if (this.creditDetail.contractMode === '02') {
			if (!this._check2(list)) {
				// 
				this.msgs = [];
				this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
				return false;
			}
		}
		// check2 end ...

		// check4 start ...
		//  
		// if (!this._check4(list)) {
		// 	this.msgs = [];
		// 	this.msgs = [{ severity: 'info', summary: '', detail: '()!' }];
		// 	return false;
		// }
		// 
		if (this.cateNumValue === '010002') {
			// 
			let tempDate;
			// let maxtargetDate = moment(new Date(this.creditDetail.endDate)).add(6, 'month').format('Y-MM-DD');
			// let targetDate = new Date(maxtargetDate); // 
			let targetDate = new Date(this.creditDetail.endDate); // 
			for (const item of list) {
				tempDate = new Date(item.guaranteeEndDate);
				if (tempDate < targetDate) {
					this.msgs = [];
					// this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
					this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
					return false;
				}
			}
		} else {
			// 
			let currentDate = new Date();
			if (!((this.data.transferCreditData && this.data.transferCreditData.varMap && (this.data.transferCreditData.varMap.dataMap['taskType'] == '10' || this.data.transferCreditData.varMap.dataMap['taskType'] == '16')) ||
				this.inValue['creditCardElement'] ||
				(this.data.transferCreditData && this.data.transferCreditData.varMap && this.data.transferCreditData.varMap.taskCategoryId == 'RUM0009'))) {
				for (const item of list) {
					if (currentDate > new Date(item.guaranteeEndDate)) {
						this.msgs = [];
						this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
						return false;
					}
				}
			}
		}
		// check4 end ...
		return true;
	}

	// check 1., 
	_check1() {
		for (const item of this.guarantor.queryList) {
			if (!item.contractNo || item.contractNo === '') {
				return true;
			}
		}
		return false;
	}

	// check 2.,
	_check2(list) {
		for (const item of list) {
			if (item.contractNo || item.contractNo !== '') {
				return true;
			}
		}
		return false;
	}
	// check 4 :,()
	_check4(list) {
		for (const item of list) {
			if (parseFloat(item.useAmt) > parseFloat(item.contractBalance) && item.contractMode != "01") {
				return false;
			}
		}
		return true;
	}

