	headleImportMaxMountGuarantee(importSingleMaxMountGuarantee) {
		let singleMaxMountGuarantees = [];
		importSingleMaxMountGuarantee.forEach(items => {
			let singleMaxMountGuarantee: Object = {};
			if (items.guarantee_mode === '') {
				singleMaxMountGuarantee['guaranteeMode'] = '101';
			}
			if (items.guarantee_mode === '') {
				singleMaxMountGuarantee['guaranteeMode'] = '201';
			}
			if (items.guarantee_mode === '') {
				singleMaxMountGuarantee['guaranteeMode'] = '301';
			}
			// added by yuanzhenghe20190415
			if (items.guarantee_mode === '') {
				singleMaxMountGuarantee['guaranteeMode'] = '302';
			}
			//
			if(this.creditDetail.applyAmt && items.contrac_balance){
				if(Number(items.contrac_balance) < Number(this.creditDetail.applyAmt) ){
					singleMaxMountGuarantee['useAmt'] = items.contrac_balance;
				} else {
					singleMaxMountGuarantee['useAmt'] = this.creditDetail.applyAmt;					
				}
			}
			singleMaxMountGuarantee['contractAmt'] = items.contrac_amt;
			singleMaxMountGuarantee['contractBalance'] = items.contrac_balance;
			singleMaxMountGuarantee['guaranteeEndDate'] = items.mature_date.replace(/(\d{4})(\d{2})(\d{2})/, '$1-$2-$3');
			singleMaxMountGuarantee['contractNo'] = items.contrac_no;
			singleMaxMountGuarantee['contractMode'] = items.contractMode;
			singleMaxMountGuarantee['custNo'] = items.client_no;
			singleMaxMountGuarantee['custName'] = items.client_name;
			singleMaxMountGuarantee['guaranteeRelation'] = items.borrower_rltnp;
			singleMaxMountGuarantee['mortgageName'] = items.mortgage_name;//
			singleMaxMountGuarantee['pledgeGoodsType'] = items.mortgage_type;//
			singleMaxMountGuarantee['pledgeName'] = items.pledge_name;//
			singleMaxMountGuarantee['houseType'] = items.house_type;//
			singleMaxMountGuarantee['questionType']=items.guarant_type;
			this.maxContractTypeList.forEach(itemse => {
				if (itemse.label == items.contract_max_type) {
					singleMaxMountGuarantee['maxContractType'] = itemse.value;
				}
			});
			singleMaxMountGuarantees.push(singleMaxMountGuarantee);
		});
		return singleMaxMountGuarantees;
	}

	// 
	formatQueryResult(qResult) {
		for (const item of qResult) {
			item.id = undefined;
			if (item.applyAmt) {
				item.applyAmt = item.applyAmt + '';
			}
			if (item.contractBalance) {
				item.contractBalance = item.contractBalance + '';
			}
			if (item.contractAmt) {
				item.contractAmt = item.contractAmt + '';
			}
			if (item.useAmt) {
				item.useAmt = item.useAmt + '';
			}
			if (item.guaranteeEndDate && typeof item.guaranteeEndDate !== 'string') {
				item.guaranteeEndDate = this.commfunc.transDateN2(item.guaranteeEndDate);
			}
			if (item.startDate && typeof item.startDate !== 'string') {
				item.startDate = this.commfunc.transDateN2(item.startDate);
			}
			if (item.endDate && typeof item.endDate !== 'string') {
				item.endDate = this.commfunc.transDateN2(item.endDate);
			}
		}
	}


