	// 
	delete(col, index, flag) {
		debugger;
		if (flag === 1) {
			if (col.cqiId) {
				if (col.contractNo) {
					let kesd = '';
					this.guarantor.queryList.forEach((item, a) => {
						if (item.contractNo == col.contractNo) {
							// kesd += item.cqiId;
							this.deleteGuarantor(item.cqiId, flag);
						}
					});
				} else {
					this.deleteGuarantor(col.cqiId, flag);
				}
			} else {
				if (col.contractNo) {
					this.guarantor.queryList.forEach((item, a) => {
						if (item.contractNo == col.contractNo) {
							this.guarantor.queryList.splice(a, 1);
						}
					});
				} else {
					this.guarantor.queryList.forEach((item, a) => {
						if (item.contractNo == col.contractNo && item.custNo == col.custNo) {
							this.guarantor.queryList.splice(a, 1);
						}
					});
				}
				this.guarantor.queryList = _.clone(this.guarantor.queryList);
				this.salesChange(this.guarantor.queryList);
			}
		} else if (flag === 2) {//
			if (col.cqiId) {
				this.deleteGuarantor(col.cqiId, flag);
			} else {
				this.mortgage.queryList.splice(index, 1);
			}
		} else if (flag === 3) {//
			if (col.cqiId) {
				this.deleteGuarantor(col.cqiId, flag);
			} else {
				this.pledge.queryList.splice(index, 1);
			}
		}else if (flag === 4) {//
			if (col.cqiId) {
				this.deleteGuarantor(col.cqiId, flag);
			} else {
				this.pledgeDeposit.queryList.splice(index, 1);
			}
		}
		this.queryFacQuestions_getGuarantor();
		this.queryFacQuestions_getMortgage();
		this.queryFacQuestions_getPledge();
		this.queryFacQuestions_getPledgeDeposit();
	}

