	deleteGuarantor(primaryKey: string, deleteNumber: Number) {
		const delelteParam = {
			cqiIds: [primaryKey]
		}

		this.creditCcmService.deleteFacQuestions(delelteParam).subscribe(data => {
			if (data.returnCode.code === 'success') {
				if (deleteNumber === 1) {
					//
					this.oprationFlag = 'delete';
					this.queryFacQuestions_getGuarantor();
				} else if (deleteNumber === 2) {
					//
					this.queryFacQuestions_getMortgage();
				} else if (deleteNumber === 3) {
					//
					this.queryFacQuestions_getPledge();
				}else if (deleteNumber === 4) {
					//
					this.queryFacQuestions_getPledgeDeposit();
				}
		
				this.msgs = [];
				this.msgs = [{ severity: 'success', summary: '', detail: '!' }];
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		});

	}

