	// 
	saveGuarantorInfo(saveData) {
		debugger;
		const items = {
			custRequestNo: this.custRequestNo,
			questionType: '02',
			questionList: saveData
		}
		this.creditCcmService.saveCreditQuestionList(items).subscribe(data => {
			if (data.returnCode.code === 'success') {
				//
				this.communicationService.toCreditelement.emit(true);
				this.queryFacQuestions_getGuarantor();
				this.queryFacQuestions_getMortgage();
				this.queryFacQuestions_getPledge();
				this.queryFacQuestions_getPledgeDeposit();
				if (data.returnCode.message.indexOf('') == -1) {
					this.saveGuarantorMsgs=[];
					this.saveGuarantorMsgs = [{ severity: 'info', summary: '', detail: data.returnCode.message }];
				}else{
					this.saveGuarantorMsgs = [];
					this.saveGuarantorMsgs = [{ severity: 'success', summary: '', detail: '!' }];
				}
			} else {
				this.queryFacQuestions_getGuarantor();
				this.saveGuarantorMsgs = [];
				this.saveGuarantorMsgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		}, error => {
			this.queryFacQuestions_getGuarantor();
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '', detail: '' });
		});
	}

