	// BI
	toBi(item) {
		this.httpService.selectCustBriefInfo({ custNo: item.custNo }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.bIReportBean.certType = data.idenType;
				this.bIReportBean.certID = data.idenNum;
				this.checkBi(item, data.idenType, data.idenNum);
			} else {
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
			}
		});

	}
	checkBi(item, cretType, cretNo) {
		this.bIReportBean.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
		let userName = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;
		let param = {
			custInfoList: [{ custNo: item.custNo, custName: item.custName }],
			userName,
			isAsyncFlag: '1'
		}
		let params = this.commfunc.handleParams(param)
		let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.checkCreditReportInfo;
		let _self = this;
		$.ajax({
			url: TZB_HTTP_CUS1,
			type: 'POST',
			data: JSON.stringify(params),
			async: false,
			headers: {
				'x-ajax': '1',
				'Content-Type': 'application/json'
			},
			dataType: 'json',
			success: function (data) {
				if (data.returnCode.code == 'success') {
					if (data.isCreate == "1") {

						_self.creatMainMsg({
							custNo: item.custNo,
							custName: item.custName,
							qryUserId: _self.bIReportBean.userId,
							cretType,
							cretNo,
							qryUserName: userName,
							accessWay: '1'
						})
					} else {
						_self.inValueReportId = data.custInfoList[0].reportId;
						_self.reportCustNm = item.custNo;
						_self.reportDisplay = true;
						_self.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
					}
				} else {
					_self.msgs = [{ severity: 'error', summary: 'Error Message', detail: data.returnCode.message }];
				}
			},
			error: function (err) {

			}
		})
	}
	creatMainMsg(param) {
		this.progress = true;
		let params = this.commfunc.handleParams(param);
		this.httpServices.createReportMainInfo(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.inValueReportId = data.reportId;
				this.reportCustNm = param.custNo;
				this.progress = false;
				this.reportDisplay = true;
			} else {
				this.progress = false;
				this.msgs = [];
				this.msgs = [{ severity: 'error', summary: 'Error Message', detail: data.returnCode.message }];
			}
		}, err => {
			this.progress = false;
			this.msgs = [];
			this.msgs = [{ severity: 'error', summary: '', detail: '' }];
		})
	}
	salesChange(item) {
		debugger;
		this.saleslist = [];
		item.forEach(items => {
			let chesk = 0;
			if (this.saleslist && items.contractNo) {
				this.saleslist.forEach(itemse => {
					if (itemse.contractNo == items.contractNo) {
						itemse.filelist.push(items);
						chesk++;
					}
				});
			}
			if (chesk == 0) {
				this.saleslist.push({
					useAmt: items.useAmt,
					contractNo: items.contractNo,
					guaranteeMode: items.guaranteeMode,
					contractAmt: items.contractAmt,
					guaranteeEndDate: items.guaranteeEndDate,
					contractBalance: items.contractBalance,
					contractMode: items.contractMode,
					maxContractType: items.maxContractType,
					filelist: [items]
				});
			}
		});
	}
	compileStr(cust) {
		return this.commfunc.compileStr(cust)
	}

	    /**
     * 1-2-3-
     */
    changeGuarantyMode(mode){
        if(mode == "1"){
			this.tabFlag = '1'; 
        } else if(mode == "2"){
            this.tabFlag = '2';      
		} else {
            this.tabFlag = '3'; 
		}
	}
	
	
    /**
     *  
     * 
     *             
     *             
     */    
    checkBusinessTypeAndShow(){
		//
        if(this.creditDetail.businessType && this.creditDetail.businessType == '06'){
			this.ContractModeFlag = false;//
			this.contractModeVal = '03';//
			this.ordinaryReadonly = true;
			this.guaranteeContractAmtFlag = false;
			this.blockedPaneladd = false;//	
        } else{
			//--_
			if(this.creditDetail.businessType && this.creditDetail.businessType == '02'){
				this.dialogSelectorFlag = true;//-- 				
				this.mortgageDialogFlag = false;//				
				this.pledgeDialogFlag = true;	//	
			}else {
				if(this.creditDetail.contractMode){// and 
					if(this.creditDetail.contractMode && this.creditDetail.contractMode == '01'){//
						this.ContractModeFlag = false;//
						this.contractModeVal = '01';//
						this.ordinaryReadonly = true;//
						this.blockedPaneladd = false;//			
					} else if(this.creditDetail.contractMode && this.creditDetail.contractMode == '03'){//
						this.ordinaryReadonly = false;//	
						this.ContractModeFlag = true;//()
						this.blockedPaneladd = false;//						
					}
				} else {	
					this.blockedPaneladd = true;//						
					this.guaranteeContractAmtFlag = false;//
				}
			}
		}
		
	}
