    //sendEmail
    // 
    query() {

        if (this.data.transferCreditData.varMap.dataMap['taskType'] == '11' && this.data.transferCreditData['taskType'] != 'approve') {
            this.istransform = true;
        }

        //
        this.isRealEstate = this.data.transferCreditData['custName'].match(/||/g) ? true : false;

        this.maskLayer1 = true;
        // // 

        let promise = new Promise(resolve => {

            // 
            this.queryCustNeedInfo(resolve);
        }).then(res => {

            if (this.data && this.data.transferCreditData.varMap.dataMap['taskType'] == '02') {
                if (this.data.transferCreditData.varMap.dataMap.cardNo) {
                    this.creditCardNum = this.data.transferCreditData.varMap.dataMap.cardNo;
                    this.creditResultInfoBean.creditCardNum = this.creditCardNum;
                }
                if (this.data.transferCreditData.varMap.dataMap.acountNo) {
                    this.creditCardNum = this.data.transferCreditData.varMap.dataMap.acountNo;

                }
                else {
                    this.creditCardNum = this.data.transferCreditData.varMap.cardNo;
                }
                this.creditResultInfoBean.creditCardNum = this.creditCardNum;
                // this.creditResultInfoBean.creditCardNum = this.data.transferCreditData.varMap.dataMap.acountNo;
                this.isxk = true;
                this.isxks = false;
                this.renewalCard(this.creditResultInfoBean.creditCardNum);
            } else {
                this.querySomething();//
            }
            this.selectCustGroupVal();
        })


        if (this.data.transferCreditData['isPreview']) {
            return;
        }

        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.tellerOrgName = chName.orgName;
        this.tellerOrgNo = commonHeader.orgId;
        this.investiGatorName = chName.userName;
        this.investiGatorId = commonHeader.userId;

        // 
        this.queryTenLeven('2');


        // 
        // this.getApplyBarCode();
        //  
        this.getCodeValues();

    }



