        this.personVeto = codeValues['personVeto'];//
        this.financeVeto = codeValues['financeVeto'];//
        this.manageVeto = codeValues['manageVeto'];//
        this.serveVeto = codeValues['serveVeto'];//
        this.otherVeto = codeValues['otherVeto'];//
        // 
        this.currencys = codeValues['CURRENCY'];
        // 
