    timeSelected(event, value) {
        let newdate = value.toLocaleDateString();
        if (this.olddate != newdate) {
            this.changes('change')
        }
        let time = new Date(this.todayDate);
        //,
        this.promaxdate = new Date(time.getFullYear(), time.getMonth() + 1, time.getDate(), 23, 59, 59);
        if (value > this.promaxdate) {
            for (let i = 0; i < this.dynamicProductBean[0].length; i++) {
                if (this.dynamicProductBean[0][i].productFeatureTypeId == "PCA0010017") {
                    this.myform.removeControl('PCA0010017');
                    this.myform.addControl('PCA0010017', this.fb.control('', [Validators.required]))
                    break;
                }
            }
            // });
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        }
    }
