    //
    getInformation(type) {
        let param = {
            custRequestId: this.data.transferCreditData.custRequestId,
            // custRequestId: '17160000',
        }
        this.creditCcmService.queryCustRequestDetail(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                const number = Number(data.adviceTerm);
                // this.creditResultInfoBean.applyAmt=  data.adviceLimit;
                if (!this.applyAmtsd) {
                    this.applyAmtsd = data.adviceLimit;
                }
                if (type != "16") {
                    this.endTime = moment(this.startTime).add(number, 'year').format('Y-MM-DD');
                }
                if (!this.decisionType) {
                    this.decisionType = data.decisionType;

                    // const number = Number('10') ;
                }
                if (!this.creditValue) {
                    this.creditValue = data.creidtLevel;
                }

            }
        })
    }
    returnValue(field) {
        for (let j = 0; j < this.dynamicProductBean[0].length; j++) {
            if (this.dynamicProductBean[0][j].productFeatureTypeId == field) {
                return this.dynamicProductBean[0][j].value;
            }
        }
    }
