    /**   5 */

    /** */
    monChange() {
        let applyAmt = this.creditResultInfoBean.applyAmt.replace(/\,/g, '');
        if (Number(applyAmt) % 100 != 0) {
            this.msgs = [{ severity: 'error', summary: '', detail: '100' }];
            this.creditResultInfoBean.applyAmt = '';
            return;
        }
        if (this.transform_flag || this.transform_money) {
            if (parseInt(this.no_money) != parseInt(applyAmt)) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '!!' }];
                return
            }
        }
    }
    // 
    deductiblePoints() {
        let applyAmt;
        let startDate
        applyAmt = this.creditResultInfoBean.applyAmt.replace(/\,/g, '');
        if (typeof this.creditResultInfoBean.startDate == 'string') {
            startDate = this.creditResultInfoBean.startDate.replace(/-/g, '')
        } else {
            startDate = this.commfunc.transDate(this.creditResultInfoBean.startDate);
        }
        const params = {
            product_code: this.data.transferCreditData.productNo, // 
            ccy: 'CNY',
            amt: Number(applyAmt),
            query_type: 'A3',
            client_no: this.data.transferCreditData.custNo,
            contract_no: this.creditResultInfoBean.contractNum,
            grant_date: startDate,
            mature_date: this.creditResultInfoBean.endDate.replace(/-/g, '')
        };
        this.creditCcmService.queryLoanInterestInfo(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                let accPet;
                accPet = this.accrualScalePct;
                this.total_deposit = Number(data.total_deposit_accrual);
                this.loan_accrual = Number(data.loan_accrual);
                this.self_deposit = (this.loan_accrual * Number(accPet)).toFixed(2);
                for (let i = 0; i < this.dynamicProductBean[0].length; i++) {
                    if (this.dynamicProductBean[0][i].productFeatureTypeId == 'PCA0010119') {
                        this.dynamicProductBean[0][i].value = String(this.total_deposit);
                        break;
                    }
                }
                for (let i = 0; i < this.dynamicProductBean[0].length; i++) {
                    if (this.dynamicProductBean[0][i].productFeatureTypeId == 'PCA0010120') {
                        this.dynamicProductBean[0][i].value = String(this.self_deposit);
                        break;
                    }
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });
    }
