    //
    onblurs(field, value, event?) {
    

        if (field == 'PCA0010048') {
            this.mainCardNo = value
            let cardNo = String(value).slice(0, 9);
            if (!cardNo) {
                return;//
            }
            for (let i = 0; i < this.mainCardList.length; i++) {
                if (this.data.transferCreditData['productNo'] == this.mainCardList[i].value) {
                    let cardZone = this.mainCardList[i].label.split('-');
                    if (!(cardNo >= cardZone[0] && cardNo <= cardZone[1])) {
                        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                        this.myform.removeControl('PCA0010048');
                        this.myform.addControl('PCA0010048', this.fb.control('', [Validators.required]))
                        return
                    }
                }
            }


            /**
            * 
            * ////
            * 
            * 
            */
            //
            if (value != '') {
                let param = {
                    "agreementId": "",
                    "creditCardNum": value
                }
                this.creditCcmService.queryCreditResultInfoByAcctNo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        if (data.resultList && data.resultList.length > 0) {
                            if (data.creditResultList[0].statusId != '1') {
                                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                                return;
                            }


                            this.isScard = false;
                            this.mainCardPassFlag = true;
                            this.custRequestNoFromScardGetMcard = data.creditResultList[0].custRequestId
                            if (this.custRequestNoFromScardGetMcard) {//
                                this.communicationService.custRequestNo.emit({ custRequestNo: this.custRequestNoFromScardGetMcard })
                            }
                            this.barCode = data.creditResultList[0].creditBarCode;
                            this.count++;
                            if (this.count == 1) {
                                this.dynamicProductBeanCopy = JSON.parse(JSON.stringify(this.dynamicProductBean));
                            }
                            this.dynamicProductBean = []
                            this.creditResultInfoBean.startDate = data.creditResultList[0].startDate || '';
                            this.creditResultInfoBean.endDate = data.creditResultList[0].endDate || '';
                            this.businessflag = true
                            this.handleDynamicProduct(data.resultList);

                            this.dynamicProductBean.forEach(items => {
                                items.forEach(item => {
                                    if (item.productFeatureTypeId == 'PCA0010034' || item.productFeatureTypeId == 'PCA0010048' || item.productFeatureTypeId == 'PCA0000210' || item.productFeatureTypeId == 'PCA0000211' || item.productFeatureTypeId == 'PCA0010044') {
                                        item.disabledFlag = false
                                    } else {
                                        item.disabledFlag = true
                                    }
                                });
                            });

                            this.creditResultInfoBean.applyAmt = this.creditResultInfoBean.applyAmt.replace(/\,/g, '');
                            this.credit_lmt = data.total;
                            if (parseInt(data.total) < parseInt(this.creditResultInfoBean.applyAmt)) {

                                this.msgs = [{ severity: 'error', summary: '', detail: `':'${data.total}` }];
                                this.creditResultInfoBean.applyAmt = '';
                            }

                        } else {
                            this.mainCardPassFlag = false;
                            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                            this.myform.removeControl('PCA0010048');
                            this.myform.addControl('PCA0010048', this.fb.control('', [Validators.required]));
                        }

                    } else {
                        this.mainCardPassFlag = false;
                        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                    }
                });
            }
        }

    }
