    //
    choiceRateCall(params) {
        this.choiceRateDisplay = false;
        if (params.passids == 1) {
            // this.createApplyBean.sigmaRatio = params.item.accrualScaleName;

            for (let i = 0; i < this.dynamicProductBean.length; i++) {
                for (let j = 0; j < this.dynamicProductBean[i].length; j++) {
                    //
                    if (this.dynamicProductBean[i][j].productFeatureTypeId == 'PCA0010004') {

                        this.dynamicProductBean[i][j].value = params.item.accrualScaleName;
                        this.accrualScaleNo = params.item.accrualScaleNo;
                        this.accrualScalePct = params.item.accrualScalePct;
                        break
                    }
                }
            }
            if (params.item.intRate) {
                this.creditResultInfoBean.applyInterestRate = params.item.intRate + '';
                this.creditResultInfoBean.executeInterestRate = params.item.intRate + '';
            }

        } else if (params.passids == 2) {
            if (params.item.intRate) {
                this.creditResultInfoBean.executeInterestRate = params.item.intRate + '';
            }

        } else if (params.passids == 3) {
            if (params.item.intRate) {
                this.creditResultInfoBean.applyInterestRate = params.item.intRate + '';
                // this.createApplyBean.sigmaRatio = '';
                this.creditResultInfoBean.executeInterestRate = params.item.intRate + '';
            }

        }
        this.changes('change');
        this.deductiblePoints();
    }
    //
    productCallTime() {
        let pro = {
            productId: this.data.transferCreditData['productNo'],
            queryType: '9',
        }
        this.creditCcmService.getAvailableProduct(pro).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let availableProduct = data.availableProduct;
                this.cateNum = availableProduct.cateNum;//
                this.subCateNum = availableProduct.subCateNum;//
                this.getContinueContractNoFlag();
                let productFeatureTypeList = [];
                let productFeatureList = [];
                let productFeatureListQX = [];
                let productFeatureListJe = [];
                if (data.productFeatureList && data.productFeatureList.length > 0) {
                    for (let i = 0; i < data.productFeatureList.length; i++) {
                        if (data.productFeatureList[i].productFeatureCategoryId == 'PC00000003') {
                            productFeatureTypeList = data.productFeatureList[i].productFeatureTypeList;
                            break
                        }
                    }
                    if (productFeatureTypeList.length > 0) {
                        for (let j = 0; j < productFeatureTypeList.length; j++) {
                            if (productFeatureTypeList[j].productFeatureTypeId == 'PCA0000172') {
                                productFeatureList = productFeatureTypeList[j].productFeatureList;
                                break
                            }
                        }
                        for (let k = 0; k < productFeatureTypeList.length; k++) {
                            if (productFeatureTypeList[k].productFeatureTypeId == 'PCA0000014') {
                                productFeatureListQX = productFeatureTypeList[k].productFeatureList;
                                break
                            }
                        }
                        for (let b = 0; b < productFeatureTypeList.length; b++) {
                            if (productFeatureTypeList[b].productFeatureTypeId == 'PCA0000012') {
                                productFeatureListJe = productFeatureTypeList[b].productFeatureList;
                                break
                            }
                        }
                    }
                    if (productFeatureListQX.length > 0) {
                        // 
                        let startMonth = productFeatureListQX[0].min; //min
                        let endMonth = productFeatureListQX[0].max; //max
                        let minStartMonth = moment(new Date(this.todayDate)).add(startMonth, 'month').format('Y-MM-DD');
                        let MaxEndMonth = moment(new Date(this.todayDate)).add(endMonth, 'month').format('Y-MM-DD');
                        // let minStartDate = moment(new Date(minStartMonth)).add('1', 'day').format('Y-MM-DD');
                        let minStartDate = minStartMonth;
                        if (startMonth && startMonth != null) {
                            this.StartMonthDate = new Date(minStartDate);
                        }
                        if (endMonth && endMonth != null) {
                            this.EndMonthDate = new Date(MaxEndMonth)
                        }
                        if (!this.creditResultInfoBean.endDate) {
                            this.creditResultInfoBean.endDate = this.StartMonthDate;
                        } else {
                            if (new Date(this.creditResultInfoBean.endDate) > this.EndMonthDate
                                || new Date(this.creditResultInfoBean.endDate) < this.StartMonthDate) {
                                this.creditResultInfoBean.endDate = this.commfunc.transDateN(this.StartMonthDate);
                            }
                        }
                    }
                    if (productFeatureListJe.length > 0) {
                        // 
                        let startminJe = productFeatureListJe[0].min; //
                        let endmaxJe = productFeatureListJe[0].max; //

                        if (startminJe && startminJe != null) {
                            this.minJe = startminJe;
                        }
                        if (endmaxJe && endmaxJe != null) {
                            this.maxJe = endmaxJe;
                        }
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    // 
    docheck(param) {
        //
        if (!this.transform_flag && !this.transform_money) {
            if (param != '' && param != null && param != undefined) {
                // var regAmt = /^[0-9]*$/;
                var regAmt = /^[0-9]{1,}[.]{0,}[0-9]{0,}$/;
                let money = param.replace(/\,/g, '')
                if (!regAmt.test(money)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    return;
                }
                // 
                if (Number(money) < Number(this.minJe)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' + this.minJe + '!' });
                    return;
                }
                if (Number(money) > Number(this.maxJe)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' + this.maxJe + '!' });
                    return;
                }
            }
        }
        if (this.commfunc.getSessionDataCH('saveMSGIdentyfy') && this.commfunc.getSessionDataCH('saveMSGIdentyfy') == 'true') {
            this.commfunc.clearSession('saveMSGIdentyfy')
        }
    }
