    //
    associationsChange(pames) {


        this.associationse.forEach(item => {
            if (pames == item.tellerId) {
                this.createApplyBean.assActorName = item.operatorName;
            }
        });

    }



    workUnit() {
        //
        this.creditCcmService.selectCustWorkunitInfo({ custNo: this.data.transferCreditData['custNo'] }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.workUnitInfoList && data.workUnitInfoList.length > 0) {
                    let arr = [];
                    data.workUnitInfoList.forEach(item => {
                        if (item.entryTime) {
                            arr.push(item.entryTime.replace(/\-/g, ''));
                        }
                    });
                    let max = Math.max.apply(this, arr);
                    let datas = data.workUnitInfoList[arr.indexOf(String(max))];
                    if (datas && datas.conmpanyCode) {
                        if (datas.conmpanyCode == 'A1') {
                            this.loanFlag = true
                        } else {
                            this.loanFlag = this.checkWorkUnit(datas.workUnit)
                        }
                        if (this.loanFlag) {
                            this.checkup(this.creditResultInfoBean.repayAmtQuarry, 1);
                            this.checkup(this.createApplyBean.loanUseRemark, 2);
                        }
                    }
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        })

        //
        this.creditCcmService.BZ_11003000062_10_CRM({ clientNo: this.data.transferCreditData['custNo'] }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.occupationType && data.occupationType == '') {
                    this.loanFlag = true;
                } else if (data.employerName) {
                    this.loanFlag = this.checkWorkUnit(data.employerName);
                }


                if (this.loanFlag) {
                    this.checkup(this.creditResultInfoBean.repayAmtQuarry, 1);
                    this.checkup(this.createApplyBean.loanUseRemark, 2);
                }
            }
        })
    }
    checkWorkUnit(unit) {
        let arr1 = ['', '', '', '', '', '', '', '', '', '', ''];
        for (let i = 0; i < arr1.length; i++) {
            if (unit.indexOf(arr1[i]) != -1) {
                return true
            };
        }
    }


    //,
    supplychain(groupId, groupName) {
        if (this.data.transferCreditData.varMap.dataMap.taskType == '11') {
            this.supplyChainF = true;
            this.myform.addControl('groupId', this.fb.control(''));
            this.myform.addControl('groupName', this.fb.control(''));
            this.groupId = groupId;
            if (groupName) {
                this.groupName = groupName;
            } else {
                this.creditCcmService.selectCustGroupInfo({ groupId }).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.groupName = data.groupName ? data.groupName : '';
                    }
                })
            }
        }
    }

    // 
    queryNowTime() {

        let nowTime = {};
        let erro = '';
        let params = this.httpService1.handleParams(nowTime)
        $.ajax({
            url: TZB_HTTP_CUS + API.queryNowTime,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: (data) => {
                if (data.returnCode.code == 'success') {
                    // 
                    if (data.nowDate) {
                        let time = data.nowDate.split(' ');//2018-01-03 00:00:00
                        this.todayDate = this.commfunc.transDateN(new Date(time[0]));
                    }
                } else {
                    erro = data.returnCode.message;
                }
            },
            error: (err) => {
                erro = '';
            }
        })
        if (erro != '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: erro });
            return
        }
    }

    selectCustGroupVal() {
        const param = {
            custNo: this.data.transferCreditData['custNo'],
            classificationType: '0',
            groupStatus: '1',
            pageNum: 1,
            pageSize: 1000,

        };
        this.creditCcmService.selectCustCustGroupList(param).subscribe(data => {
            if (data.returnCode.code === 'success') {
                debugger;
                this.groupListArray = [];
                if (data.groupList && data.groupList.length > 0) {
                    this.groupListArray = [{ label: '', value: '' }];
                    data.groupList.forEach(item => {
                        this.groupListArray.push({ label: item.groupName, value: item.groupId });
                    });
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });
    }

